	module.exports = {"data":{"markdownRemark":{"fileAbsolutePath":"/home/luanorlandi/git/angeloocana/src/pages/blog/linux/arch/install-yaourt.pt.md","html":"<p>Yaourt é o gerenciador de pacotes da comunidade para o Arch Linux.</p>\n<p>Quando você usa o <strong>pacman</strong> (gerenciador de pacotes oficial) você precisa usar <strong>sudo</strong>,\npara o <strong>yaourt</strong> você não precisa.</p>\n<p>Abra o arquivo abaixo para edição:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">sudo</span> vim /etc/pacman.conf\n</code></pre>\n      </div>\n<p>Adicione isto no final do arquivo:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-conf\"><code>    [archlinuxfr]\n    SigLevel = Never\n    Server = http://repo.archlinux.fr/$arch</code></pre>\n      </div>\n<p>Instalar yaourt</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">sudo</span> pacman -Sy yaourt\n</code></pre>\n      </div>","excerpt":"Yaourt é o gerenciador de pacotes da comunidade para o Arch Linux. Quando você usa o  pacman  (gerenciador de pacotes oficial) você precisa…","fields":{"tagSlugs":[{"tag":"Linux","link":"/pt/tags/linux/"},{"tag":"Arch","link":"/pt/tags/arch/"}],"slug":"/pt/blog/linux/arch/install-yaourt/","readNextPosts":[{"excerpt":"Extrair .rar Extrair .zip Instalar unzip Extrair arquivo","frontmatter":{"title":"Extrair Arquivos no Arch linux","date":"2017-07-31"},"fields":{"slug":"/pt/blog/linux/arch/extract-files/","langKey":"pt"}},{"excerpt":"Se você quer aprender linux de verdade, Arch é a minha distribuição favorita, esteja preparado pra quebrar muito a cabeça, mas relaxa que…","frontmatter":{"title":"Instalar Arch linux + wifi and xfce4","date":"2017-08-04"},"fields":{"slug":"/pt/blog/linux/arch/install-arch-linux-wifi-xfce4/","langKey":"pt"}},{"excerpt":"Vim é um editor de texto da velha guarda, ele é executado no console, é o mais rápido de todos.\nÉ a melhor opção para acessar servidores…","frontmatter":{"title":"Vim ajuda com comandos","date":"2017-08-08"},"fields":{"slug":"/pt/blog/vim/vim-help-commands/","langKey":"pt"}}]},"frontmatter":{"youtubeId":null,"title":"Como instalar Yaourt no Arch Linux","tags":["Linux","Arch"],"date":"2017-08-30","structuredData":{"type":"TechArticle","author":"angeloocana","dependencies":"Arch Linux","proficiencyLevel":"Beginner","articleSection":"Arch Linux","pageEnd":null,"pageStart":null,"pagination":null,"about":{"name":"Arch Linux","alternateName":"Arch","description":"lightweight and flexible Linux® distribution that tries to Keep It Simple.","identifier":"arch-linux","image":null,"sameAs":"https://www.archlinux.org/"},"accessMode":"textual","accessModeSufficient":"textual","accessibilityAPI":"ARIA","accessibilityControl":"fullKeyboardControl, fullTouchControl, fullMouseControl","accessibilitySummary":null,"aggregateRating":"...","audience":"...","alternativeHeadline":null,"comment":"...","commentCount":"...","contentLocation":"...","dateCreated":"2017-08-26","dateModified":"2017-08-30","datePublished":"2017-08-30","discussionUrl":"...","educationalUse":"...","isAccessibleForFree":true,"isFamilyFriendly":true,"keywords":"...","locationCreated":"Ottawa CA","thumbnailUrl":"...","version":1,"video":"..."}}}},"pathContext":{"path":"/pt/blog/linux/arch/install-yaourt/","langKey":"pt"}}

